.schedule {
    @include abstract-card();
    overflow-x: scroll;
    position: relative;
  
    li::before {
      display: none;
    }
  
    ul.schedule-timeline,
    ul.schedule-group,
    ul.schedule-events {
      margin-top: 0;
      padding-left: 0;
    }
  
    ul.schedule-timeline {
      margin: 40px auto 0;
      position: absolute;
      width: 100%;
    }
  
    .schedule-time {
      @extend .fs-2;
      color: $grey-dk-000;
      height: 40px;
      margin: 0;
      padding: $sp-2;
      position: relative;
  
      &::after {
        background-color: $border-color;
        content: '';
        height: 1px;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
      }
    }
  
    .schedule-group {
      display: flex;
      margin-bottom: 0;
      position: relative;
    }
  
    .schedule-day {
      border-left: $border $border-color;
      flex: 1 0 0;
      margin: 0;
      min-width: 130px;
  
      &:first-of-type {
        border-left: 0;
      }
    }
  
    h2.schedule-header {
      align-items: center;
      display: flex;
      font-size: 18px !important;
      height: 40px;
      justify-content: center;
      margin: 0;
    }
  
    .schedule-events {
      display: flex;
      padding: 0;
      position: relative;
    }
  
    .schedule-event {
      background-color: $grey-dk-000;
      border-radius: $border-radius;
      box-shadow: 0 10px 20px rgba(0, 0, 0, .1), inset 0 -3px 0 rgba(0, 0, 0, .2);
      color: $block-text-color !important;
      float: left;
      height: 100%;
      margin: 0;
      padding: $sp-1 $sp-2;
      position: absolute;
      width: 100%;
  
      .name {
        @extend .fs-3, .fw-700;
      }
  
      .time,
      .location,
      .instructor {
        @extend .fs-2;
      }
  
      &.lecture {
        background-color: $lecture-color;
        color: $block-text-color !important;

      }
  
      &.disc {
        background-color: $disc-color
      }
  
      &.office-hours {
        background-color: $office-hours-color;
      }
      &.coffee-chats {
        background-color: $yellow-200;
      }
      &.discussion {
        background-color: $purple-000;
      }
    }
  }
